export const t = () => null;


export const REQUEST_REGISTER_PAGE = 'REQUEST_REGISTER_PAGE';
export const openRegisterPageActionCreator = () => ({
  type: REQUEST_REGISTER_PAGE,
  payload: {
    isRegistering: true
  }
});

export const SET_USERNAME_INPUT = 'SET_USERNAME_INPUT';
export const setUsernameActionCreator = username => ({
  type: SET_USERNAME_INPUT,
  payload: {
    username: username || ''
  }
});

export const SET_PASSWORD_INPUT = 'SET_PASSWORD_INPUT';
export const setPasswordActionCreator = password => ({
  type: SET_PASSWORD_INPUT,
  payload: {
    password: password || ''
  }
});

export const HANDLE_REGISTER = 'HANDLE_REGISTER';
export const handleRegisterActionCreator = ({ username, password }) => ({
  type: HANDLE_REGISTER,
  payload: {
    username,
    password,
    isRegistering: false
  }
});
